import React, { Component } from 'react';
import PanelLabel from './PanelLabel.js'
import {setAPIToken, getVenues} from '../../actions.js'

class SelectVenue extends Component {
    constructor(props) {
      super(props);
      setAPIToken(props.token)
      this.state = {
         isVisible: props.isVisible,
         token: props.token,
         venues: []
      }
      this.fetchVenues(this.props.session)
    }

    fetchVenues(service){
        var component = this;
        getVenues(service, function(data){
            component.setState({
                venues: data
            });
        });
    }

    selectVenue(id){
        this.props.selectVenue(id);
    }

    render() {
        return (
            <div className={this.props.isVisible ? 'two-third' : 'hide-component'}>
                <PanelLabel index='4' simpleText='Select a proper' boldText=' venue' />
                <VenueSelector venues={this.state.venues} venueSelectedId={this.props.venueSelectedId} selectVenue={this.selectVenue.bind(this)}/>
            </div>
        );
    }
}

class VenueSelector extends Component {
    constructor(props) {
        super(props);
        this.state = {
        }
    }

    selectVenue(id){
        this.props.selectVenue(id);
    }

    render() {
console.log("venues", this.props.venues)
        return (
            <ul className="listed-elements-x2">
                { this.props.venues !== undefined && Object.keys(this.props.venues).length > 0 ? this.props.venues.map((venue, i) => <VenueItem id={venue.id} name={venue.name} label={venue.address} venueSelectedId={this.props.venueSelectedId} selectVenue={this.selectVenue.bind(this)} />) : null}
            </ul>
        );
    }
}


class VenueItem extends Component {

    selectVenue(event){
        console.log("VenueItem:")
        console.log(event.target.id)
        this.props.selectVenue(event.target.id);
    }

    render() {
        return (
            <li className={this.props.venueSelectedId==this.props.id ? 'active' : ''} id={this.props.id} onClick={this.selectVenue.bind(this)}>
                <p id={this.props.id}>{this.props.name}</p>
                <span className="f-right" id={this.props.id}>{this.props.label}</span>
            </li>
        );
    }
}


export default SelectVenue;



