import React, { Component } from 'react';
import PanelLabel from './PanelLabel.js'
import {setAPIToken, getServices} from '../../actions.js'

class SelectService extends Component {
    constructor(props) {
      super(props);
      setAPIToken(props.token)
      this.state = {
         isVisible: props.isVisible,
         token: props.token,
         services: [],
      }
      var component = this;
      getServices(function(data){
        component.setState({
            services: data
        })
      })
    }

    setNewClient(fullName, email){
        this.props.setNewClient(fullName, email)
    }

    selectService(event){
        this.props.selectService(event.target.id)
    }

    render() {
        return (
            <div className={this.props.isVisible ? 'two-third' : 'hide-component'}>
                <PanelLabel index='2' simpleText='Which type of' boldText=' Service?' />
                <ServiceSelector token={this.state.token} selectService={this.selectService.bind(this)} services={this.state.services} selectedId={this.props.serviceId} />
            </div>
        );
    }
}


class ServiceSelector extends Component {
    constructor(props){
        super(props);
        setAPIToken(props.token)
        this.state = {
            isVisible: props.isVisible,
            token: props.token
        }
    }

    selectService(event){
        this.props.selectService(event)
    }

    render() {
        return (
            <ul className="listed-elements-x2">
                {this.props.services.map((service, i) => <ServiceItem selectedId={this.props.selectedId} selectService={this.selectService.bind(this)} id = {service.id} label = {service.name} length = {service.length} price = {service.price} />)}
            </ul>
        );
    }
}

class ServiceItem extends Component {

    constructor(props){
        super(props)
    }

    selectService(event){
        this.props.selectService(event)
    }

    render() {
        var itemClassName = this.props.selectedId == this.props.id ? "active" : ""
        return (
            <li className={itemClassName} onClick={this.selectService.bind(this)} id={this.props.id}>
                <p id={this.props.id}>
                    {this.props.label}
                </p>
                <span className="f-right" id={this.props.id}>
                    {this.props.length}  ${this.props.price}
                </span>
            </li>
        );
    }
}


export default SelectService;


