import React, { Component } from 'react';
import PanelLabel from './PanelLabel.js'
import {setAPIToken} from '../../actions.js'

class Summary extends Component {
    constructor(props) {
      super(props);
      setAPIToken(props.token)
      this.state = {
         isVisible: props.isVisible,
         token: props.token,
         venues: []
      }
    }

    setPaymentOption(event){
        if(event.target.id === 'alreadyPaid'){
            var alreadyPaid = event.target.className==='unselected' ? true : false;
            var sendPaymentLink = alreadyPaid ? false : this.props.sendPaymentLink;
            this.props.setPaymentOption(alreadyPaid, sendPaymentLink);
        }else if(event.target.id === 'sendPaymentLink'){
            var sendPaymentLink = event.target.className==='unselected' ? true : false;
            var alreadyPaid = sendPaymentLink ? false : this.props.alreadyPaid;
            this.props.setPaymentOption(alreadyPaid, sendPaymentLink);
        }
    }

    reloadPage(event){
        window.location.href = this.props.successUrl;
    }


    render() {
        return (
            <div className={this.props.isVisible ? 'two-third' : 'hide-component'}>
                <div className="one-third full-height bg-gray">

                </div>
                <div className="two-third">
                    <div className="top-content">
                        <p className="t-lyon">{this.props.appointment.session_date}</p>
                        <p className="t-lyon">{this.props.appointment.session_time}</p>
                        <h3 className="t-proxima">
                            <strong>
                                {this.props.appointment.session_name}
                            </strong>
                            { ' ' }
                             with
                            { ' ' }
                            <strong>
                                 {this.props.appointment.client_name}
                            </strong>
                        </h3>
                        <p className="t-lyon c-light-gray">
                            Venue {this.props.appointment.venue_name}
                        </p>
                        <p className="t-lyon">
                            ${this.props.appointment.session_price} MXN
                        </p>
                        <ul className="listed-elements-bullets mb20">
                            <li className={this.props.alreadyPaid ? 'selected' : 'unselected'} id='alreadyPaid' onClick={this.setPaymentOption.bind(this)}>The client already paid</li>
                            <li className={this.props.sendPaymentLink ? 'selected' : 'unselected'} id='sendPaymentLink' onClick={this.setPaymentOption.bind(this)}>Send the client payment link</li>
                        </ul>
                    </div>
                    <div className="bottom-content">
                        <button id="go-to-appointment" className={this.props.editAppointment ? 'hide-component' : 'stroke-btn-gray enabled'} onClick={this.reloadPage.bind(this)}>Go to my Bookings</button>
                        <button id="add-appointment" className={this.props.editAppointment ? 'stroke-btn-gray enabled' : 'hide-component'} onClick={this.props.submitAddAppointment}>Add Appointment</button>
                    </div>
                </div>
            </div>
        );
    }
}



export default Summary;


