import React, { Component } from 'react';

import './css/index.css';
import './css/reset.css';
import './css/slick.css';
import './css/fixing.css';
import ModalBooking from './module/ModalBooking/ModalBooking.js'

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
        token: props.token
    };
  }

  render() {
    return (
      <div className="App">
        <ModalBooking token={this.state.token}  />
      </div>
    );
  }
}

export default App;



