import $ from 'jquery';
var actions = require('./endPoints.js');

var API_TOKEN = ''

export function setAPIToken(token){
    API_TOKEN = token;
}

export function searchClientAction(search, callback){
    var url = actions.endPoints.clients
    $.ajax({
        url: url,
        method: "GET",
        data: {
            search : search
        },
        dataType: "json",
        headers: {
            "Authorization": "Token " + API_TOKEN
        },
    }).done(function(data){
        callback(data.results);
    }).fail(function(jqXHR, textStatus, errorThrown){
        callback([]);
    });
}

export function addClientAction(full_name, email, callback){
    var url = actions.endPoints.addClient
    $.ajax({
        url: url,
        method: "POST",
        data: {
            full_name : full_name,
            email: email,
        },
        dataType: "json",
        headers: {
            "Authorization": "Token " + API_TOKEN
        },
    }).done(function(data){
        callback(data, true);
    }).fail(function(jqXHR, textStatus, errorThrown){
        callback({}, false);
    });
}


export function getServices(callback){
    var url = actions.endPoints.services
    $.ajax({
        url: url,
        method: "GET",
        dataType: "json",
        headers: {
            "Authorization": "Token " + API_TOKEN
        },
    }).done(function(data){
        callback(data.results);
    }).fail(function(jqXHR, textStatus, errorThrown){
        callback([]);
    });
}


export function getCalender(date, serviceId, callback){
    var url = actions.endPoints.calendar
    $.ajax({
        url: url,
        method: "GET",
        dataType: "json",
        data: {
            date : date,
            service: serviceId
        },
        headers: {
            "Authorization": "Token " + API_TOKEN
        },
    }).done(function(data){
        callback(data);
    }).fail(function(jqXHR, textStatus, errorThrown){
        callback({});
    });
}

export function getVenues(service, callback){
    var url = actions.endPoints.venues
    $.ajax({
        url: url,
        method: "GET",
        dataType: "json",
        data: {
            service : service
        },
        headers: {
            "Authorization": "Token " + API_TOKEN
        },
    }).done(function(data){
        callback(data.results);
    }).fail(function(jqXHR, textStatus, errorThrown){
        callback({});
    });
}

export function previewAppointment(client_id, session_id, date, hour, venue_id, callback){
    var url = actions.endPoints.previewAppointment
    $.ajax({
        url: url,
        method: "GET",
        dataType: "json",
        data: {
            client_id: client_id,
            session_id: session_id,
            date: date,
            hour: hour,
            venue_id: venue_id,
        },
        headers: {
            "Authorization": "Token " + API_TOKEN
        },
    }).done(function(data){
        callback(data);
    }).fail(function(jqXHR, textStatus, errorThrown){
        callback({});
    });
}

export function addAppointment(custome_venue, online_call, venue_id, client_id, service_id, date, time, already_paid, send_payment_link, callback){
    var url = actions.endPoints.addAppointment
    $.ajax({
        url: url,
        method: "POST",
        dataType: "json",
        data: {
            custome_venue: custome_venue,
            online_call: online_call,
            venue_id: venue_id,
            client_id: client_id,
            service_id: service_id,
            date: date,
            time: time,
            already_paid: already_paid,
            send_payment_link: send_payment_link,
        },
        headers: {
            "Authorization": "Token " + API_TOKEN
        },
    }).done(function(data){
        callback(data);
    }).fail(function(jqXHR, textStatus, errorThrown){
        callback({});
    });
}



