import React, { Component } from 'react';


class PanelLabel extends Component {
    constructor(props) {
        super(props);

        this.state = {
            index: props.index,
            simpleText: props.simpleText,
            boldText: props.boldText,
        }
    }
    render() {
        return (
            <p className="c-light-gray t-lyon mb40">
                {this.state.index}. {this.state.simpleText}
                <span className="c-aqua">
                    {this.state.boldText}
                </span>
            </p>
        );
    }
}

export default PanelLabel;


