import React, { Component } from 'react';
import PanelLabel from './PanelLabel.js'
import {searchClientAction, setAPIToken} from '../../actions.js'



class SelectClient extends Component {
    constructor(props) {
      super(props);
      setAPIToken(props.token)
      this.state = {
         isVisible: props.isVisible,
         token: props.token
      }
    }

    setNewClient(fullName, email){
        this.props.setNewClient(fullName, email)
    }

    selectClient(id){
        this.props.selectClient(id)
    }

    render() {
        return (
            <div className={this.props.isVisible ? 'two-third' : 'hide-component'}>
                <PanelLabel index='1' simpleText='Book with' boldText=' whom?' />
                <ClientSelector token={this.state.token} selectClient={this.selectClient.bind(this)}/>
                <AddClient setNewClient={this.setNewClient.bind(this)} />
            </div>
        );
    }
}

class AddClient extends Component {
    constructor(props){
        super(props)
        this.state={
            fullName: '',
            email: '',
        }
    }

    setValue(event){
        if(event.target.id === 'fullName'){
            this.setState({
                fullName: event.target.value
            })
        }else{
            this.setState({
                email: event.target.value
            })
        }
    }

    validInput(){
        this.props.setNewClient(this.state.fullName, this.state.email)
    }

    render(){
        return (
            <div className="align-bottom">
                <p className="t-proxima mb20 c-gray">
                    <strong>
                        Or, enter a new client:
                    </strong>
                </p>
                <div className="two-col columns">
                    <input className="small-box" type="text" id="fullName" placeholder="Full name" value={this.state.fullName} onChange={this.setValue.bind(this)} onBlur={this.validInput.bind(this)} />
                </div>
                <div className="two-col columns">
                    <input className="small-box" type="text" id="email" placeholder="E-mail" value={this.state.email} onChange={this.setValue.bind(this)} onBlur={this.validInput.bind(this)} />
                </div>
            </div>
        )
    }
}


class ClientSelector extends Component {
    constructor(props) {
        super(props);

        this.state = {
            token: props.token,
            clients: [],
            displayClientList: false,
            clientName: ''
        }

        this.handleSearchChange = this.handleSearchChange.bind(this);
    }

    selectClient(event){
        this.setState({
            displayClientList: false,
            clientName: event.target.textContent
        })
        this.props.selectClient(event.target.id)
    }

    handleSearchChange(event){
        this.setState({
            clientName: event.target.value
        })
        var component = this;
        if(event.target.value.trim().length > 0){
            searchClientAction(event.target.value, function(data){
                var display = data.length > 0 ? true : false;
                component.setState({
                    displayClientList: display,
                    clients: data,
                });
            });
        }else{
            component.setState({
                displayClientList: false,
                clients: [],
            });
        }
    }

    render() {
        return (
            <div className="one-col columns">
                <div className="search-input">
                    <input className="big-box" value={this.state.clientName} list="languages" type="search" placeholder="Search by Name" onChange={this.handleSearchChange.bind(this)} />
                    <ClientList selectClient={this.selectClient.bind(this)} clients={this.state.clients} display={this.state.displayClientList}/>
                </div>
            </div>
        );
    }
}


class ClientList extends Component {
    constructor(props) {
        super(props);

        this.state = {
            showList: props.display,
            clients: props.clients,
        }
    }

    selectClient(event){
        this.props.selectClient(event)
    }

    render() {
        return (
            <div className={this.props.display ? 'suggestion-list show' : 'suggestion-list'}>
                <ul>
                    {this.props.clients.map((client, i) => <ClientItem selectClient={this.selectClient.bind(this)} id = {client.id} label = {client.full_name} />)}
                </ul>
            </div>
        );
    }
}

class ClientItem extends Component{

    selectClient(event){
        this.props.selectClient(event)
    }

    render(){
        return (
            <li onClick={this.selectClient.bind(this)} id={this.props.id}>{this.props.label}</li>
        );
    }
}


export default SelectClient;


