import React, { Component } from 'react';
import PanelLabel from './PanelLabel.js'
import {setAPIToken, getCalender} from '../../actions.js'

class SelectDateTime extends Component {
    constructor(props) {
      super(props);
      setAPIToken(props.token)
      this.state = {
         isVisible: props.isVisible,
         token: props.token,
         appointments: [],
         hours: [],
         smallDate: null,
         minDate: '',
         currentDate: '',
         timeId: '-1',
         timeLabel: null
      }
      this.changeDate(null)
    }

    changeDate(date){
        var component = this;
        var serviceId = this.props.selectedServiceId;
        getCalender(date, serviceId, function(data){
            component.setState({
                appointments: data.appointments,
                hours: data.date_times,
                smallDate: data.small_date,
                minDate: data.min_date,
                currentDate: date === null ? data.min_date : date,
                timeId: '-1',
                timeLabel: null,
            })
        })
    }

    selectHour(id, label){
        var currentDate = this.state.currentDate
        this.setState({
            timeId: id,
            timeLabel: label
        }, () => this.props.selectHour(id, label, currentDate))
    }

    render() {
        return (
            <div className={this.props.isVisible ? 'two-third' : 'hide-component'}>
                <PanelLabel index='3' simpleText='Select the right' boldText=' time' />
                <DateTimeSelector minDate={this.state.minDate} date={this.state.currentDate} selectHour={this.selectHour.bind(this)} changeDate={this.changeDate.bind(this)} timeSelectedId={this.state.timeId} hours={this.state.hours} />
                <Appointments appointments={this.state.appointments} smallDate={this.state.smallDate} />
            </div>
        );
    }
}


class DateTimeSelector extends Component {


    changeDate(event){
        this.props.changeDate(event.target.value)
    }

    selectHour(event){
        var id = event.target.value
        var label = event.target.textContent
        this.props.selectHour(id, label)
    }

    render() {
        return (
            <div className="calendar-input">
                <div className="one-col columns">
                    <input className="big-box datepicker hasDatepicker" type="date" min={this.props.minDate} value={this.props.date} onChange={this.changeDate.bind(this)}/>
                </div>
                <div className="time">
                    <select className="big-input" onChange={this.selectHour.bind(this)}>
                        <option value='-1' selected={this.props.timeSelectedId=='-1'}>
                            Select Time
                        </option>
                        { this.props.hours !== undefined ? this.props.hours.map((hour, i) => <option value={hour.id} selected={this.props.timeSelectedId==hour.id} onClick={this.selectHour.bind(this)}>{hour.time}</option>) : null}
                    </select>
                </div>
            </div>
        );
    }
}


class Appointments extends Component {
    render () {
        return (
            <div className="align-bottom">
                <p className="t-proxima c-gray"><strong>Appointments on {this.props.smallDate}:</strong></p>
                <ul className="listed-elements-x1">
                    {this.props.appointments !== undefined && this.props.appointments !== null ?this.props.appointments.map((appointment, i) => <AppointmentItem begins={appointment.begins} ends={appointment.ends} client_name={appointment.client.full_name} />) : null}
                </ul>
            </div>
        );
    }
}


class AppointmentItem extends Component {
    render () {
        return (
            <li>
                <p>
                    {this.props.begins}  {this.props.ends}
                </p>
                <span>
                    {this.props.client_name}
                </span>
            </li>
        );
    }
}


export default SelectDateTime;



