import React, { Component } from 'react';
import Menu from './Menu.js';

import SelectClient from './SelectClient.js'
import SelectService from './SelectService.js'
import SelectDateTime from './SelectDateTime.js'
import SelectVenue from './SelectVenue.js'
import Summary from './Summary.js';
import {setAPIToken, previewAppointment, addClientAction, addAppointment} from '../../actions.js'

class ModalBooking extends Component {
    constructor(props) {
        super(props);
        setAPIToken(props.token)
        this.state = {
            selectClientVisible: true,
            selectServiceVisible: false,
            selectHourVisible: false,
            selectVenueVisible: false,
            summaryVisible: false,
            token: props.token,
            nextEnabled: false,
            addingClient: false,
            clientFullName: null,
            clientEmail: null,
            clientId: null,
            serviceId: null,
            dateSelected: null,
            timeSelected: null,
            venueSelected: null,
            customeVenue: null,
            alreadyPaid: false,
            sendPaymentLink: false,
            editAppointment: true,
            successfullyAdded: false,
            onlineCall: false,
            previewAppointment: {
                session_name: '',
                session_time: '',
                session_date: '',
                session_price: '',
                client_name: '',
                venue_name: '',
                date_time_available: false,
            },
            step: 1,
            menuData: [
                {
                   "name":"Client",
                   "isActive":true,
                   "step": 1,
                },
                {
                   "name":"Service",
                   "isActive":false,
                   "step": 2,
                },
                {
                   "name":"Time",
                   "isActive":false,
                   "step": 3,
                },
                {
                   "name":"Venue",
                   "isActive":false,
                   "step": 4,
                },
                {
                   "name":"Summary",
                   "isActive":false,
                   "step": 5,
                },
            ]
        }
    }

    updateMenu(){
        this.setState({
            menuData: [
                {
                   "name":"Client",
                   "isActive":this.state.selectClientVisible
                },
                {
                   "name":"Service",
                   "isActive":this.state.selectServiceVisible
                },
                {
                   "name":"Time",
                   "isActive":this.state.selectHourVisible
                },
                {
                   "name":"Venue",
                   "isActive":this.state.selectVenueVisible
                },
                {
                   "name":"Summary",
                   "isActive":this.state.summaryVisible
                },
            ]
        });
    }

    enableNextButton(){
        this.setState({
            nextEnabled: true
        })
    }

    disableNextButton(){
        this.setState({
            nextEnabled: false
        })
    }

    setNewClient(fullName, email){
        var component = this;
        if(fullName.trim().length && email.trim().length){
            addClientAction(fullName, email, function(data, added){
                if(added){
                    component.setState({
                        addingClient: false,
                        clientId: data.id,
                        clientFullName: data.full_name,
                        clientEmail: data.email
                    }, () => component.enableNextButton())
                }else{
                    component.setState({
                        addingClient: true,
                        clientFullName: fullName,
                        clientEmail: email
                    }, () => component.enableNextButton())
                }

            });
        }else{
            this.setState({
                addingClient: false,
                clientFullName: null,
                clientEmail: null
            })
            this.disableNextButton()
        }
    }

    selectClient(id){
        if(id === null){
            this.setState({
                addingClient: false,
                clientId: null,
            })
            this.disableNextButton()
        }else{
            this.setState({
                addingClient: false,
                clientId: id,
            })
            this.enableNextButton()
        }
    }

    selectHour(id, label, date){
        if(id === '-1' && label === null){
            this.setState({
                timeSelected: null,
                hourLabel: null,
                dateSelected: null,
            })
            this.disableNextButton()
        }else{
            this.setState({
                timeSelected: id,
                hourLabel: label,
                dateSelected: date,
            })
            this.enableNextButton()
        }
    }

    selectService(id){
        if(id === null){
            this.setState({
                serviceId: null,
            })
            this.disableNextButton()
        }else{
            this.setState({
                serviceId: id,
            })
            this.enableNextButton()
        }
    }

    selectDateTime(date, time){
        console.log("date: " + date + ' time: ' + time)
        if(date === null && time === null){
            this.setState({
                dateSelected: null,
                timeSelected: null,
            })
            this.disableNextButton()
        }else{
            this.setState({
                dateSelected: date,
                timeSelected: time,
            })
            this.enableNextButton()
        }
    }

    selectVenue(id){
        if(id === null){
            this.setState({
                venueSelected: null,
            })
            this.disableNextButton()
        }else{
            this.setState({
                venueSelected: id,
            })
            this.enableNextButton()
        }
    }

    doNext(event){
        if(event.target.classList.contains('enabled')){
            if(this.state.step === 1){ // The user has selected a client or add the info of a new client
                this.setState({
                    selectClientVisible: false,
                    selectServiceVisible: true,
                    step: 2,
                }, () => this.afterDoNext())
            }else if(this.state.step === 2){ // The user has selected a service
                this.setState({
                    selectServiceVisible: false,
                    selectHourVisible: true,
                    step: 3,
                }, () => this.afterDoNext())
            }else if(this.state.step === 3){ // The user has selected a date and an hour
                this.setState({
                    selectHourVisible: false,
                    selectVenueVisible: true,
                    step: 4,
                }, () => this.afterDoNext())
            }else if(this.state.step === 4){ // The user has selected a venue
                this.setState({
                    selectVenueVisible: false,
                    summaryVisible: true,
                    step: 5,
                }, () => this.afterDoNext())
            }
        }
        this.disableNextButton();
    }

    afterDoNext(){
        this.getPreviewAppointment()
        this.updateMenu();
    }

    getPreviewAppointment(){
        var component = this;
        var client_id = this.state.clientId;
        var session_id = this.state.serviceId;
        var date = this.state.dateSelected;
        var hour = this.state.timeSelected;
        var venue_id = this.state.venueSelected;
        previewAppointment(client_id, session_id, date, hour, venue_id, function(data){
            component.setState({
                previewAppointment: data,
            });
        });
    }

    setPaymentOption(alreadyPaid, sendPaymentLink){
        this.setState({
            alreadyPaid: alreadyPaid,
            sendPaymentLink: sendPaymentLink,
        });
    }

    submitAddAppointment(){
        var custome_venue = this.state.customeVenue;
        var online_call = this.state.onlineCall;
        var venue_id = this.state.venueSelected;
        var client_id = this.state.clientId;
        var service_id = this.state.serviceId;
        var date = this.state.dateSelected;
        var time = this.state.timeSelected;
        var already_paid = this.state.alreadyPaid;
        var send_payment_link = this.state.sendPaymentLink;
        var component = this;
        addAppointment(custome_venue, online_call, venue_id, client_id, service_id, date, time, already_paid, send_payment_link, function(data){
            var editAppointment = (data.id === undefined);
            var successfullyAdded = !editAppointment;
            component.setState({
                editAppointment: editAppointment,
                successfullyAdded: successfullyAdded,
            });
        })
    }

    switchSection(event){
        event.preventDefault();
        if(this.state.step != event.target.id){
//            this.setState({
//                step: event.target.id
//            }, () => this.afterDoNext());
        }
    }

    render() {
        return (
            <div className="modal-booking mb40">
                <div className={this.state.successfullyAdded ? 'two-col message-notification blue align-top t-proxima' : 'hide-component'}>Booking successful! <strong>{this.state.previewAppointment.client_name}</strong> will be notified by email</div>
                <div className="p30">
                    <SelectClient setNewClient={this.setNewClient.bind(this)} selectClient={this.selectClient.bind(this)} isVisible={this.state.selectClientVisible} token={this.state.token} />
                    <SelectService selectService={this.selectService.bind(this)} isVisible={this.state.selectServiceVisible} token={this.state.token} serviceId={this.state.serviceId}/>
                    <SelectDateTime selectedServiceId={this.state.serviceId} selectDateTime={this.selectDateTime.bind(this)} isVisible={this.state.selectHourVisible} token={this.state.token} dateSelected={this.state.dateSelected} timeSelected={this.state.timeSelected} selectHour={this.selectHour.bind(this)}/>
                    <SelectVenue selectVenue={this.selectVenue.bind(this)} isVisible={this.state.selectVenueVisible} token={this.state.token} venueSelectedId={this.state.venueSelected} session={this.state.serviceId} />
                    <Summary isVisible={this.state.summaryVisible} token={this.state.token} appointment={this.state.previewAppointment} alreadyPaid={this.state.alreadyPaid} sendPaymentLink={this.state.sendPaymentLink} setPaymentOption={this.setPaymentOption.bind(this)} submitAddAppointment={this.submitAddAppointment.bind(this)} editAppointment={this.state.editAppointment} successUrl={this.props.successUrl}/>
                    <Menu nextEnabled={this.state.nextEnabled} doNext={this.doNext.bind(this)} menuData={this.state.menuData} switchSection={this.switchSection.bind(this)}/>
                </div>
            </div>
        );
    }
}

export default ModalBooking;



