import React, { Component } from 'react';

class Menu extends Component {
    constructor() {
      super();

      this.state = {

      }
    }

    render() {
        var nextClassName = this.props.nextEnabled === true ? "stroke-btn-gray align-bottom enabled" : "stroke-btn-gray align-bottom"
        return (
            <div className="one-third">
                <ul className="modal-booking-nav">
                    {this.props.menuData.map((item, i) => <MenuItem isActive = {item.isActive} step={item.step} label = {item.name} switchSection={this.props.switchSection}/>)}
                </ul>
               <button className={nextClassName}  onClick={this.props.doNext}>Next</button>
            </div>
        );
    }
}

class MenuItem extends Component{
    render(){
        var className = '';
        if(this.props.isActive){
            className = 'active';
        }
        return (
            <li><a href="/" id={this.props.step} onClick={this.props.switchSection}  className={className}>{this.props.label}</a></li>
        );
    }
}

export default Menu;


